
.Home_Loyout_Container{
    margin-bottom: 100px;
    width: 100%;
    height: 535px;

    display: flex;
    align-items: center;
    background: #F3F5F7;
    
    img{
        width: 50%;
        height: 100%;
    }

    .Home_Loyout_Info{
        width: 50%;
        display: flex;
        flex-direction: column;
        padding:72px;    

        p{
            width: 100px;
            height: 28px;
            margin-top: 24px;
            font-weight: 500;
            font-style: Medium;
            font-size: 16px;
            line-height: 28px;
            letter-spacing: -0.4px;
            
            border-bottom:1px solid black;
            cursor: pointer;

            &:hover .Home_Banner_Strell{
                transform:translateX(5px);
            }
        }
        .Home_Loyout_Text{
            width: 452px;
            height:200px;

            display: flex;
            flex-direction: column;
            gap: 16px;

            h3{
                width: 452px;
                height: 88px;

                font-weight: 600;
                font-size: 16px;
                line-height: 16px;
                letter-spacing: 0%;
                text-transform: uppercase;

                color: #377DFF;
            }

            h1{
                width: 452px;
                height: 88px;

                font-weight: 500;
                font-style: Medium;
                font-size: 40px;
                line-height: 44px;
                letter-spacing: -0.4px;

            }

            h2{
                width: 452;
                height: 64;

                font-weight: 400;
                font-size: 20px;
                line-height: 32px;
                letter-spacing: 0px;
            }
        }

        .Home_Banner_Strell {
            display: inline-block; 
            width: 13.6px;
            height: 1px;
            border-radius: 1px;
            background-color: black;
            vertical-align: middle; 
            position: relative;
            cursor: pointer;
            margin-left: 6px;

            z-index: 2;

            &::before,
            &::after {
                content: '';
                position: absolute;
                right: -1px;
                width: 7px;
                height: 1px;
                background-color: black;
                border-radius: 1px;
                transform-origin: right center;
            }

            &::before {
                transform: rotate(45deg);
                top: 0px;
            }

            &::after {
                transform: rotate(-45deg);
                bottom: 0px;
            }


            transition: transform 0.2s ease;
            &:hover {
                transform: translateX(3px);
            }
        }
    }    
}

@media(max-width:450px){
    .Home_Loyout_Container{
       height: 734px;
       flex-direction: column; 

       img{
            width: 100%;
            height: 375px;
        }
        
        .Home_Loyout_Info{
            width: 100%;
            height: 367px;
            border: 1px solid black;            
            padding: 58px 32px;
            
            p{
                margin-top: 24px;
                width: 100px;
                height: 24px;

                font-weight: 500;
                font-style: Medium;
                font-size: 14px;
                line-height: 24px;
                letter-spacing: 0px;
            }

            .Home_Loyout_Text{
                width:311px;
                height: 202px;

                h3{
                    width: 311px;
                    height: 16px;

                    font-weight: 700;
                    font-size: 16px;
                    line-height: 16px;
                    letter-spacing: 0%;
                    text-transform: uppercase;
                }
                
                h1{
                    width: 100%;
                    height: 76px;

                    font-weight: 500;
                    font-style: Medium;
                    font-size: 34px;
                    line-height: 38px;
                    letter-spacing: -0.6px;
                }

                h2{
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 26px;
                    letter-spacing: 0px;
                }
            }
        }
    }
}